import Logo from '../assets/main-footer-logo.png'
import './footer.css'

const Footer = () => {
  return (
    <footer className="footer">
      <div className="footer__container">
        <div className="footer__logo">
          <img className="footer__container__logo" src={Logo} alt="logo" />
        </div>
        <div className="footer__container__text">
          <p className="footer__container__social">
            <a
              href="https://gamesunited.co/privacy-policy"
              className="footer__container__social__item"
              target="_blank"
              rel="noopener noreferrer"
            >
              Privacy Policy
            </a>
          </p>
          <p className="footer__container__social">
            <a
              href="https://www.gamesunited.co/terms-of-service"
              className="footer__container__social__item"
              target="_blank"
              rel="noopener noreferrer"
            >
              Term Of Use
            </a>
          </p>
        </div>
      </div>
      <div className="footer__info__wrapper">
        <p className="footer__info"> 2022 Powered by Alwox</p>
      </div>
    </footer>
  )
}

export default Footer
