import React, { useState, useEffect } from 'react'
import './App.css'

import { jsPDF } from 'jspdf'
import 'jspdf-autotable'
import { Table } from 'react-bootstrap'
import { ExcelExport } from '@progress/kendo-react-excel-export'

import Footer from './footer/footer.component'

import DATA from './data.js'
let batu = []
for (let i = 0; i < 20; i++) {
  batu.push(0)
}

function App() {
  const [data, setData] = useState(DATA)
  // const [score, setScore] = useState(0)
  const [totalScore, setTotalScore] = useState(0)
  const [handleUpdate, setHandleUpdate] = useState(false)

  const _export = React.useRef(null)

  const excelExport = () => {
    if (_export.current !== null) {
      _export.current.save()
    }
  }
  const handleScore = (id, score) => {
    const newData = data.map((item) => {
      if (item.id === id) {
        batu[id] = Number(score)
        return { ...item, score: score }
      }
      return item
    })
  }

  const score = DATA.map(function (item) {
    return item.score
  })

  // const percantageItems = score.filter(function (item) {
  //   return typeof item === 'number' && item !== 0
  // })

  function median() {
    let sum = 0
    let total = 0
    for (let i = 0; i < batu.length; i++) {
      if (!isNaN(batu[i])) {
        sum += batu[i]
      } else {
        sum += 0
        total++
      }
    }
    return sum / (batu.length - total)
  }

  const exportPDF = () => {
    const unit = 'pt'
    const size = 'A4' // Use A1, A2, A3 or A4
    const orientation = 'portrait' // portrait or landscape

    const marginLeft = 40
    const doc = new jsPDF(orientation, unit, size)

    doc.setFontSize(15)

    const title = 'NEW PDF'
    const headers = [['Score Name', 'Percentage']]

    const data = DATA.map((elt) => [elt.subtitle, elt.score])

    const totalPercentage = ['Total Percentage', median()]

    let content = {
      startY: 50,
      head: headers,
      body: data,
      foot: [totalPercentage],
    }

    doc.text(title, marginLeft, 40)
    doc.autoTable(content)
    doc.save('score-percentage.pdf')
  }

  function exportTableToExcel(tableID, filename = '') {
    var downloadLink
    var dataType = 'application/vnd.ms-excel'
    var tableSelect = document.getElementById(tableID)
    var tableHTML = tableSelect.outerHTML.replace(/ /g, '%20')

    // Specify file name
    filename = filename ? filename + '.xls' : 'excel_data.xls'

    // Create download link element
    downloadLink = document.createElement('a')

    document.body.appendChild(downloadLink)

    if (navigator.msSaveOrOpenBlob) {
      var blob = new Blob(['\ufeff', tableHTML], {
        type: dataType,
      })
      navigator.msSaveOrOpenBlob(blob, filename)
    } else {
      // Create a link to the file
      downloadLink.href = 'data:' + dataType + ', ' + tableHTML

      // Setting the file name
      downloadLink.download = filename

      //triggering the function
      downloadLink.click()
    }
  }

  return (
    <>
      <div className="App">
        <h1 className="header">Game Name</h1>
        <ExcelExport ddata={data} ref={_export}>
          <Table striped bordered hover data={data} ref={_export}>
            <thead>
              <tr className="main-header">
                <th colSpan={3}>HYPER-CASUAL MVP POWER SCORE CHECK LIST</th>
              </tr>
              <tr>
                <th className="ideation-header">Ideation</th>
                <th className="score-header">Score</th>
                <th className="score-header">Aciklama</th>
              </tr>
            </thead>
            <tbody>
              {data.slice(0, 7).map((item) => (
                <tr key={item.id}>
                  <td>{item.subtitle}</td>
                  {/* <td>{item.score}</td> */}
                  <input
                    placeholder="Send Score"
                    onChange={(e) => {
                      handleScore(item.id, e.target.value)
                      setHandleUpdate(!handleUpdate)
                    }}
                  />
                  <td style={{ backgroundColor: '#D0D7E1' }}>{item.trTitle}</td>
                </tr>
              ))}
            </tbody>
            <thead>
              <tr>
                <th className="development-header">Development</th>
                <th className="score-header">Score</th>
                <th style={{ backgroundColor: '#D0D7E1' }}></th>
              </tr>
            </thead>
            <tbody>
              {data.slice(7, 21).map((item) => (
                <tr key={item.id}>
                  <td>{item.subtitle}</td>
                  <input
                    placeholder="Send Score"
                    onChange={(e) => {
                      handleScore(item.id, e.target.value)
                      setHandleUpdate(!handleUpdate)
                    }}
                  />
                  <td style={{ backgroundColor: '#D0D7E1' }}>{item.trTitle}</td>
                </tr>
              ))}

              <tr className="power-score">
                <td>Power Score:</td>
                <td className="total-score"> {median()}</td>
                <th style={{ backgroundColor: '#ff7074' }}></th>
              </tr>
            </tbody>
          </Table>
        </ExcelExport>
        <button onClick={excelExport} className="convert-button">
          Export PDF
        </button>
      </div>
      <Footer />
    </>
  )
}

export default App
